import { useContext } from "react";
import { AiFillDelete } from "react-icons/ai";
import { PostListContext } from "../store/post-list-store"; // Import the context

const Post = ({ post }) => {
    const { deletePost } = useContext(PostListContext); // Use the context
    return (
      <div className="card post-card" style={{ width: "30rem", position: "relative" }}>
        <div className="card-body">
          <h5 className="card-title">{post.title}
          <span className="position-absolute top-0 end-0 translate-middle badge rounded-pill bg-danger"
            onClick={() => deletePost(post.id)}>
            <AiFillDelete/>
          </span>
          </h5>
          <p className="card-text">{post.body}</p>
          {post.tags.map((tag) => (<span key={tag} className="badge text-bg-primary hashtag">{tag}</span>))}

          <div className="alert alert-success reactions" role="alert">
              This post has been reacted by {post.reactions} people.
          </div>
        </div>
      </div>
    );
};

export default Post;
